"use client"

import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Monitor, Smartphone, Tablet, Globe } from "lucide-react"

interface StatsTableProps {
  data: Array<{ [key: string]: string | number }>
  type: "country" | "device" | "referrer"
}

export function StatsTable({ data, type }: StatsTableProps) {
  const getIcon = (value: string) => {
    if (type === "device") {
      if (value.toLowerCase().includes("mobile")) return <Smartphone className="h-4 w-4" />
      if (value.toLowerCase().includes("tablet")) return <Tablet className="h-4 w-4" />
      return <Monitor className="h-4 w-4" />
    }
    if (type === "country") {
      return <Globe className="h-4 w-4" />
    }
    return null
  }

  const getLabel = (item: any) => {
    if (type === "country") return item.country
    if (type === "device") return item.device
    if (type === "referrer") return item.referrer
    return ""
  }

  const getPercentage = (clicks: number) => {
    const total = data.reduce((sum, item) => sum + (item.clicks as number), 0)
    return ((clicks / total) * 100).toFixed(1)
  }

  return (
    <Table>
      <TableHeader>
        <TableRow>
          <TableHead>Source</TableHead>
          <TableHead className="text-right">Clicks</TableHead>
          <TableHead className="text-right">Percentage</TableHead>
        </TableRow>
      </TableHeader>
      <TableBody>
        {data.slice(0, 10).map((item, index) => (
          <TableRow key={index}>
            <TableCell className="font-medium">
              <div className="flex items-center gap-2">
                {getIcon(getLabel(item))}
                <span>{getLabel(item)}</span>
              </div>
            </TableCell>
            <TableCell className="text-right">{(item.clicks as number).toLocaleString()}</TableCell>
            <TableCell className="text-right">{getPercentage(item.clicks as number)}%</TableCell>
          </TableRow>
        ))}
      </TableBody>
    </Table>
  )
}
